.goods-item {
    position: relative;
    color: $greyish-black;
}

.goods-item__link {
    text-decoration: none;

    @media (min-width: $tablet-width) {
        grid-column: 3/5;
        grid-row: 2/6;
    }

    @media (min-width: $desktop-width) {
        grid-column: 4/7;
        grid-row: 1/6;
    }
}

.goods-item__title-link {
    text-decoration: none;

    @media (min-width: $tablet-width) {
        grid-column: 1/4;
        grid-row: 1;
    }

    @media (min-width: $desktop-width) {
        grid-column: 2/7;
        grid-row: 1;
        margin-top: 82px;
        z-index: 1;
    }
}

.goods-item__title {
    font-weight: bold;
    font-size: 25px;
    line-height: 30px;
    margin: 0;
    color: $black;



}

.goods-item__description {
    margin: 0;
    font-size: 15px;
    margin-top: 17px;

    @media (min-width: $tablet-width) {
        margin: 0;
        grid-column: 1/3;
        grid-row: 2;
        margin-top: 39px;
        font-size: 17px;
        line-height: 30px;
        margin-right: 35px;
    }

    @media (min-width: $desktop-width) {
        grid-column: 2/4;
        grid-row: 2;
        margin-right: 50px;
        margin-top: 35px;
    }
}

.goods-item__properties {
    margin: 0;
    margin-top: 28px;
    margin-bottom: 21px;
    font-size: 15px;
    line-height: 20px;

    @media (min-width: $tablet-width) {
        grid-column: 1/3;
        grid-row: 3;
        margin-top: 38px;
        align-self: start;
    }

    @media (min-width: $desktop-width) {
        grid-column: 2/4;
        grid-row: 3;
        margin-top: 52px;
    }
}

.goods-item__property {
    display: flex;
    padding-bottom: 20px;

    @media (min-width: $tablet-width) {
        padding-bottom: 11px;
        padding-top: 8px;
        margin-right: 45px;
        border-bottom: 1px solid black;
    }
}

.goods-item__property-name {
    width: 22%;

    @media (min-width: $tablet-width) {
        width: 20%;
    }
}

.goods-item__property-value {
    @media (min-width: $tablet-width) {}
}

.goods-item__img {
    background-color: $white;

    @media (min-width: $tablet-width) {
        margin-top: 50px;
        width: 100%;
    }

    @media (min-width: $desktop-width) {
        margin-top: 41px;
    }
}

.goods-item__price {

    font-weight: bold;
    @include center-x;
    font-size: 17px;
    margin: 0;
    padding: 0;
    margin-top: 21px;
    margin-bottom: 22px;

    @media (min-width: $tablet-width) {
        grid-column: 1/3;
        grid-row: 4;
        font-size: 20px;
        line-height: 24px;
        margin: 0;
        margin-right: 50px;
    }

    @media (min-width: $desktop-width) {
        grid-column: 2/4;
        grid-row: 4;
        margin-right: 58px;
    }
}

.goods-item__price--value {
    margin-left: 5px;

    @media (min-width: $tablet-width) {
        margin-left: 6px;
    }
}

.goods-item__button {
    font-size: 18px;
    line-height: 20px;
    font-weight: bold;
    text-decoration: none;
    text-transform: uppercase;
    color: $greyish-black;
    border: 2px solid $ligther-grey;
    @include center-x;
    padding-bottom: 15px;
    padding-top: 14px;

    @media (min-width: $tablet-width) {
        grid-column: 1/3;
        grid-row: 5;
        font-size: 17px;
        margin-right: 43px;
        margin-bottom: 6px;
        padding-bottom: 13px;
        padding-top: 14px;
        margin-top: 32px;
    }

    @media (min-width: $desktop-width) {
        grid-column: 2/4;
        grid-row: 5;
        margin-right: 57px;
    }
}
