.promo-menu {
    font-size: 15px;
    line-height: 24px;
    color: $greyish-black;

    @media (min-width: $tablet-width) {
        justify-content: flex-end;
    }

    @media (min-width: $desktop-width) {}
}

.promo-menu__list {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;

    @media (min-width: $desktop-width) {
        width: 100%;
    }


}

.promo-menu__item {
    display: none;
    font-weight: bold;
    font-size: 15px;
    line-height: 24px;
    margin-right: 30px;

    @media (min-width: $desktop-width) {
        display: block;
        margin-right: 62px;
    }
}

.promo-menu__item--rigth {
    @media (min-width: $tablet-width) {
        display: block;
        margin: 0;
        margin-left: auto;
        font-weight: 400;
    }
}

.promo-menu__item--link {
    width: 100%;
    display: flex;
    color: $black;
    font-weight: bold;
    text-decoration: none;
}

.promo-menu__item--rigth-link {
    font-weight: 400;
}
